import QRCode from "qrcode";

const qrOptions = {
    color: {
        dark: "#c5007f",
        light: "#ffffff",
    },
};

export async function buildQRCode(data) {
    let qrCodeURL
    try {
        qrCodeURL = await QRCode.toDataURL(data, qrOptions);
        console.log("QR Code Generated:", qrCodeURL);

    } catch (err) {
        console.error("Error generating QR code:", err);
    }
    return qrCodeURL || ""
}
